// Breadcrumb navigation
//
// The path to the current page in the form of a list of links.
//
// Markup: breadcrumb.twig
//
// Style guide: navigation.breadcrumb

.au-breadcrumbs {
  display: none;
  @include AU-media(sm) {
    display: block;
  }

  @media print {
    display: block;
  }

  * + &--flush {
    margin-top: 0
  }
}
